*{
    font-family: 'Poppins', sans-serif;
}
a:link {
    text-decoration: none;
}  
a:visited {
    text-decoration: none;
}  
a:hover {
    text-decoration: none;
    color: #ff324d;
}  
a:active {
    color: #ff324d;
}
.mo{
    color: #ff324d;
}
.top-header{
    height: 32px;
    padding: 10px 0;
    font-size: 16px;
    border-bottom: 1px solid #eee;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.contaner{
    margin: 0 200px;
}
.left-d{
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: flex-start;
}
.lang img{
    border-radius: 100%;
    height: 16px;
    width: 16px;
    margin-right: 6px;
}
.lang span{
    text-transform: capitalize;
    font-size: 14px;
    color: #333;
    padding: 5px 7px 5px 10px;
    display: inline-block;
}
.d-arrow{
    color: #878787;
    font-size:x-small ;

}

.cantury span{
    padding: 5px 7px 5px 10px;
    font-size: 14px;
    color: #333;
}
.cantury{
    padding-right: 10px;
}

.d-phone{
    color: #878787;
    padding-right: 7px;
    font-weight: 300;
}
.num span{
    font-size: 14px;
}

.right-d{
    padding-right: 200px;
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: flex-end;
    cursor: pointer;
}
.r-icon{
    height: 12px;
    width: 14px;
    color: #878787;
    font-size: smaller;
    padding-right: 5px;
}
.right-d div{
    padding: 2px 15px 2px 0;
    font-size: 14px;
    color: #292b2c;
}
.shuffle:hover {
  color: red;
}
.wishkist:hover{
    color: red;
}
.login:hover{
    color: red;
}
.dowen-header{
    position: sticky;
    top: 0;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.logo{
    padding: 10px 0 10px 200px;
    height: 70px;
    width: 180px;
}
.categores{
    width: 790px;
}
.categores ul{
    list-style: none;
    display: flex;
    flex-direction: row;
    padding-left: 250px;

}
.categores li{
    padding: 22px 10px ;
    font-size: 14px;
    color: #292b2c;
    cursor: pointer;
}
li .d-arrow{
    padding-left: 5px ;
    font-size: 12px;

}

.hide {
    background-color: #fff;
    position: absolute;
    display: block;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
    margin-top: 20px;
    pointer-events: none;
    height: 200px;
    width: 225px;
    z-index: 100;
}
.hide ul{
    padding: 0;
    display: block;
    z-index: 100;
}
.hide li{
    padding: 7px 5px 7px 25px;
    z-index: 100;
}

.p-hide {
    background-color: #fff;
    position: absolute;
    display: block;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
    margin-top: 20px;
    pointer-events: none;
    height: 220px;
    width: 225px;
    z-index: 100;
}
.p-hide ul{
    padding: 0;
    display: block;
    z-index: 100;
}
.p-hide li{
    padding: 7px 5px 7px 25px;
    z-index: 100;
}
.b-hide ul{
    padding: 0;
    display: block;
    z-index: 100;
}
.b-hide li{
    padding: 7px 5px 7px 25px;
    z-index: 100;
}

.home{
    color: red;
}
.home .d-arrow{
    color: red;
}
.home:hover .hide{
    opacity: 1;
    visibility: visible;
    transition: all 0.35s ease-in-out;
}
