.summercolliction{
    position: relative;
    height: 420px;
    background-color: #F4F9FC;
    margin-top: 70px;
    display: flex;
    justify-content: center;
}
.summer-img{
    position: absolute;
    bottom: 0px;
    left: 175px;
    height: 460px;
    width: 520px;
    padding: 0 15px;
}
.summer-info{
    position: absolute;
    right: 120px;
    top: 130px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
.summer-info p{
    height: 25px;
    width: 540px;
    margin: 0 0 10px;
    color: #ff324d;
    font-size: 16px;
    font-weight: 400px;
}
.summer-info h2{
    margin: 0;
    padding: 0;
    color: #292b2c;
}
.summer-info h5{
    height: 25px;
    font-size: 20px;
    margin: 0 0 24px;
    font-weight: 500;
}
.summer-info a{
    padding: 12px 35px;
    text-align: center;
    color: #fff;
    border: 1px solid #ff324d;
    background-color: #ff324d;
    transition: all 0.3s  ease-in-out;
}
.summer-info a:hover{
    background-color: #fff;
    color: #ff324d;
} 