.footer{
    padding: 100px 0 70px;
    background-color: #202325;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    color: #fff;
}
.shopwise{
    height: 250px;
    width: 280px;
}
.shopwise p{
    width: 230px;
    font-size: 14px;
    margin:0 0 25px 0;
    line-height: 28px;
}
.shopwise img{
    margin-bottom: 10px;
}
.s-icons ul{
    width: 210px;
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
    margin: 0;
    padding: 0;
}
.mm:hover{
    color: red;
    transition: all 0.3s ease-in-out;
}

.list h6{
    height: 20px;
    width: 160px;
    font-size: 18px;
    margin: 10px 0 25px;
    font-weight: 600;
}
.list ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    margin: 0;
    padding: 0;
}
.list li{
    padding: 0 0 10px;
    font-size: 14px;
    cursor: pointer;
}

.li-icon{
    padding-right: 5px;
}

.mm{
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 26px;
}