.slider{
    z-index: -1;
}
.slid{
    height: 450px;
    z-index: -1;
}
.photo img{
    height: 450px;
    width: 600px;
}
.slide-1{
    background-color: #ecf4ff;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
}
.slide-2{
    background-color: #d2fef1;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
}
.slide-3{
    background-color: #ffe4d3;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
}
.side-div{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.side-div h2{
    font-size: 60px;
    margin: 0 0 20px;
}
.side-div span{
    font-size: 20px;
    margin: 0 0 16px;
}
.shop {
    height: 25px;
    width: 95px;
    padding:12px 35px;
    margin: 0;
    text-align: center;
    cursor: pointer;
    background-color: #FF324D;
    border: 1px solid #FF324D;
    color: #fff;
    transition: all 0.3s ease-in-out;
}
.shop:hover {
    background-color: transparent;
    color: #FF324D;
}