.nothing{
    text-align: center;
    font-size: 36px;
    font-weight: 700;
}
.cart{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}
.card p{
    margin: 0;
}
.card button{
    height: 30px;
    width: 130px;
    text-align: center;
    border: 1px solid red;
    background-color: red;
    color: #fff;
    transition: all 0.3s ease-in-out;
}
.card button:hover{
    background-color: transparent;
    color: black;
    transition: all 0.3s ease-in-out;
}