.collection{
    height: 300px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding-top: 65px;
    padding-bottom: 20px;
}
.banner img{
    position: relative;
    height: 300px;
    width: 540px;
    padding: 0 15px;
    transition: transform 0.3s ease;
}

.banner:hover img {
  transform: scale(1.06); 
}

.banner .title1{
    position: relative;
    height: 140px;
    width: 245px;
    left: 270px;
    bottom: 235px;
    padding: 20px;
}
.title1 h5{
    height: 25px;
    width: 230px;
    font-size: 20px;
    font-weight: 300;
    margin: 0 0 8px;
    color: #29282c;
}
.title1 h3{
    height: 40px;
    width: 230px;
    font-size: 32px;
    margin: 0 0 8px;
    font-weight: 700;
}
.title1 span{
    height: 20px;
    width: 80px;
    margin: 0;
    font-size: 16px;
    cursor: pointer;
}

