.rules{
    padding: 100px 0 70px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.freedlivery{
    width: 370px;
    height: 100px;
    padding: 0 40px;
    margin: 0 0 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-right: 0.5px solid #687188;
}
.top-icon{
    width: 290px;
    height: 50px;
    height: 46px;
    width: 46px;
    font-size: 16px;
    color: #687188;
    margin: 0 0 15px;
}
.freedlivery h5{
    width: 290px;
    font-size: 20px;
    margin: 0 0 8px;
    font-weight: 500;   
}
.freedlivery p{
    width: 290px;
    height: 55px;
    font-size: 16px;
    color: #687188;
    font-weight: 400;
}

