.subscribe{
    background-color: #ff324d;
    padding: 50px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}
.subscribe h3{
    width: 540px;
    height: 35px;
    font-size: 28px;
    font-weight: 700;
    margin: 0;
    color: #fff;
}
.subscribe input{
    width: 450px;
    height: 30px;
    padding: 8px 15px;
    font-size: 16px;
    font-weight: 400;
}
.subscribe button{
    height: 50px;
    padding: 12px 35px;
    background-color: #343a40;
    color: #fff;
}