.login-container {
    max-width: 400px;
    height: 350px;
    margin-left: 450px;
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 20px;
    border: 1px solid #ccc;
    box-shadow: 0 0 15px rgba(0,0,0,0.3);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }
  
.login-container h2 {
    text-align: left;
    margin-bottom: 20px;
    padding-top: 10px;
}
  
.login-container form {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
  
.login-container label {
    margin-bottom: 10px;
}
  
.login-container input[type='email'],
.login-container input[type='password'] {
    width: 375px;
    height: 30px;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    margin-bottom: 13px;
}
  
.login-container button {
    height: 45px;
    padding: 10px 20px;
    background-color: red;
    font-size: 20px;
    font-weight: 700;
    color: #fff;
    border: none;
    border: 1px solid red;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
.login-container button:hover{
    background-color: transparent;
    color: black;
} 
.login-container p {
    color: red;
   margin-bottom: 10px;
}