.singlep{
  height: auto;
  width: 90%;
  margin: 20px auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
}

@media (min-width: 768px) {
  .singlep {
    flex-direction: row;
    height: 300px;
    gap: 80px;
  }
}
.single-info{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.single-info span{
  text-align: center;
}

.add-cart2{
  border: 1px solid red;
}